export default {
    /**--------------------------color-start----------------------**/
    /**
     * rgbRandom
     * @description rgb颜色随机
     * @return rgb string
     */
    rgbRandom: function () {
        // rgb颜色随机
        var r = Math.floor(Math.random() * 256);
        var g = Math.floor(Math.random() * 256);
        var b = Math.floor(Math.random() * 256);
        return "(" + r + "," + g + "," + b + ")";
    },
    /**
     * color16Random
     * @description 十六进制颜色随机
     * @return color string
     */
    color16Random: function () {
        // 十六进制颜色随机
        var r = Math.floor(Math.random() * 256);
        var g = Math.floor(Math.random() * 256);
        var b = Math.floor(Math.random() * 256);
        return "#" + r.toString(16) + g.toString(16) + b.toString(16);
    },
    /**
     * colorToRgb
     * @description color转换成rgb颜色
     * @param color
     */
    colorToRgb: function (color) {
        var numberColor = color.replace(/^#*/, '');
        if (numberColor.length === 3) {
            numberColor = Array.from(numberColor)
                .reduce(function (result, number) {
                result.push("" + number, "" + number);
                return result;
            }, [])
                .join();
        }
        // 都变成6位了
        var rgb = [];
        for (var i = 0; i < numberColor.length; i += 2) {
            rgb.push(parseInt("" + numberColor[i] + numberColor[i + 1], 16));
        }
        return rgb;
    },
    /**
     * rgbToColor
     * @description rgb转换成color
     * @param r
     * @param g
     * @param b
     * @return color string
     */
    rgbToColor: function (r, g, b) {
        return "" + r.toString(16) + g.toString(16) + b.toString(16);
    },
};
